.movie__info__content {
  background-color: var(--bg__color);
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  min-height: 92vh;
}

.movie__info .movie__img {
  width: auto;
  max-height: 100%;
  border-radius: 15px;
  box-shadow: 2px 4px var(--color_secondary2);
  overflow: hidden;
}

.movie__info__title-wrapper {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  padding: 1.5em 0px 0.2em 0px;
}

.movie__info__title {
  color: var(--text-color-secondary);
  font-size: 2.5em;
  text-align: center;
  margin-bottom: 0.4em;
}

.rating {
  color: var(--text-color-primary);
}

.movie__info__desc {
  max-width: 50%;
  margin-top: 1em;
  padding-bottom: 2em;
}

.movie__info__desc-text {
  color: var(--text-color-primary);
  font-size: 1.2em;
  line-height: 2em;
  margin: 0 auto;
  text-align: justify;
}

.summary {
  font-size: 1.1em;
  font-weight: 200;
  font-style: italic;
  margin-bottom: 0.5em;
}

.movie__info__content .btn {
  margin: 2em;
}

@media only screen and (max-width: 480px) and (min-width: 320px) {
  .movie__info .movie__img {
    max-width: 80vw;
    max-height: 800px;
  }

  .movie__info__title {
    font-size: 4vh;
  }

  .movie__info__desc {
    max-width: 90%;
  }

  .movie__info__desc-text {
    font-size: 2vh;
  }

  .summary {
    font-size: 1.2em;
  }
}
